
import { getAjaxData, postAjaxData } from "../../api/api";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

import DictSelector from "../../components/dict/DictSelector";
import { mapState } from "vuex";
export default {
directives: {
TransferDom
},
mounted() {
let page = this.$route.query.page;
let id = this.$route.query.id;
if (page) {
this.$router.push({
path: `${page}/${id}`
});
}
},
data() {
return {};
},



beforeRouteEnter(to, from, next) {
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
} catch (err) {}
next();
},
methods: {
load() {
this.$nextTick().then(() => {
let hei = document.body.clientHeight,
offset = this.top || this.$refs.body_bottom.offsetTop;
this.$refs.body_bottom.style.height = `${hei - offset}px`;
});
},
goto(path, name) {
if (this.isShow(name)) {
this.$router.push(path);
} else {
this.$vux.toast.show({
type: "warn",
text: "您暂无此模块权限"
});
}
},
dictSelectHanler(event) {


},
close() {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
},
isShow(type) {


return this.$store.state.auth.find(item => item.name == type);
},
goMdtPage() {

plus.webview.open(
`http://www.giftmall.top/Mobile/LoginApi/login?oauth=maidiyun&maidiyunToken=${
this.$store.state.token
}&from=crm`
);
}
},
components: {
TransferDom,
XHeader,
Grid,
GridItem,
GroupTitle,
DictSelector
},
beforeRouteLeave(to, from, next) {
if (to.fullPath === "/TaskWork") {

}
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
this.$router.back();
});
} catch (err) {}
next(vm => {});
}
};



