
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'

export default {
components: {
PopupPicker,
PopupRadio
},
props: {
title: {
default: "选择器"
},
type: {
default: "4"
},
dictId: 0
},
data() {
return {
value: this.dictId,
dict: {},
list: []
};
},

mounted() {
this.$store.dispatch("initDictPromise", 4).then(() => {
this.initData();
});
},
watch: {
dictId: (newValue, oldValue) => {
console.log(newValue);
this.value = newValue;
}
},
methods: {
initData() {
let _list = this.$store.state.dict[this.type];
_list.forEach(element => {
this.list.push({
value: element.DictName,
key: element.ID
});
});





},
onShow() {
console.log("SHow");
},
onHide() {
console.log("Hide", this.value);
},
onChange(event) {
console.log("change");
let item = this.list.find(item => item.key == event);
this.dict = item;
this.$emit("dictSelected", item);
},
getDict() {
return this.dict;
}
}
};



